

.containerFooter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 30%;
    height: 30%;
    border-radius: 50%;
    
    border-radius: 20px;
}

.containerFooter::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background: linear-gradient(45deg,#00aeff,#1900ff);
    width: 100%;
    filter: blur(50px);
    height: 100%;
    background-size: 500%;
    z-index: -1;
    animation: rotate 3ms linear infinite;

}

.profilePictureFooter{
    width: 50%;
    border-radius: 50%;
}

@keyframes rotate{
    to{ transform: rotate(360deg); }
  }

.backgroundThree{
    content: "";
    position: absolute;
    background: rgb(39,84,143);
    background: radial-gradient(circle, rgba(39,84,143,1) 28%, rgba(41,32,128,1) 100%);
    background: white;
    z-index: -3;
    width: 100%;
    height: 100%;
}
