.containerAbilities{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 60%;
    height: 60%;
    background: #11101d;
    border-radius: 20px;
}

.containerAbilities::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background: linear-gradient(45deg,#fb0094,#6200ff, #0059ff, #00ffea, #711ab8);
    width: 100%;
    height: 100%;
    background-size: 500%;
    z-index: -1;
    filter: blur(30px);
    -webkit-animation: spin 15s ease infinite;
    -moz-animation: spin 15s ease infinite;
    animation: asd 15s ease infinite;
}



@-webkit-keyframes asd {
    from {
      background-position: 0px 0px; }
  
    to {
      background-position: 100% 0px; } 
  }

@-webkit-keyframes asd {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@-moz-keyframes asd {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@keyframes asd { 
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}

.backgroundTWo{
    content: "";
    position: absolute;
    background: rgb(15,12,41);
    background: linear-gradient(90deg, #0f0c29 0%, rgba(35,38,64,1) 50%, rgba(15,12,41,1) 100%);
    z-index: -3;
    width: 100%;
    height: 100%;
    overflow: hidden
}

.containerListasAbilities{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.backgroundList{
    background: #afc5f3;
}





