.containerPresentation{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    margin: 0;
    background: transparent;
}

.backgroundOne{
    content: "";
    position: absolute;
    background: #213e63;
    background: linear-gradient(0deg, #213e63 0%, rgba(122,124,227,1) 50%, rgba(221,161,254,1) 100%);
    z-index: -1;
    width: 100%;
    height: 100vh;
}

.containerProfilePicture{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 350px;
    height: 350px;
    border-radius: 50%;
    background: transparent; 
}

.containerProfilePicture::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background: #370daa;
    width: 100%;
    filter: blur(30px);
    height: 100%;
    background-size: 500%;
    z-index: -1;
    border-radius: 50%;
    animation: rotateShadowPicture 5s linear infinite;
}


.ProfilePicture{
    width: 90%;
    border-radius: 50%;
    z-index: 1;
}

.welcomeMessage{
    font-size: 1.875rem;
    color:rgb(43, 58, 104);
}

.informationStyleOne{
    width: 60%;
    text-align: justify;
    font-size: 18px;
}

.myNameStyle{
    font-size: 1.875rem;
    color: white;
    margin-bottom: 15px;
}

.ImgBack{
    width: 35%;
    position: relative;
    object-fit: cover;
    height: 100%;
}

@media screen and (max-width: 1300px){
    .containerProfilePicture{
        height: 150px!important;
        width: 150px;
    }
      .containerProfile{
        width: 100%;
    }
    .ProfilePicture{
        width: 100%;
    }
}


@media screen and (max-width: 1100px){
    .informationStyleOne{
        width: 80%;
    }
    .containerProfile{
        width: 100%;
    }
    .containerProfilePicture{
        height: 100px!important;
        width: 100px!important;
    }
    .myNameStyle{
        margin-bottom: 0px;
    }
}



@media screen and (max-width: 800px){
    .informationStyleOne{
        width: 98%;
    }
    .containerProfilePicture{
        height: 80px!important;
        width: 80px!important;
    }
    .ImgBack{
        display: none;
    }
    .BackgroundParticles{
        display: none!important;
    }
    .myNameStyle,.welcomeMessage{
        font-size: 1.25rem;
    }

}